/* eslint-disable prettier/prettier */
<template>
  <div id="app">
    <Tinder
      ref="tinder"
      key-name="id"
      :queue.sync="queue"
      :max="3"
      :offset-y="10"
      allow-down
      @submit="onSubmit"
    >
      <template slot-scope="scope">
        <div class="pic" style="text-align: center;padding-top: 2rem">
          <img
            width="305"
            height="225"
            :src="
              `https://stockcharts.com/c-sc/sc?r=&chart=${scope.data.id},uu[305,a]dacayaci[pb20!b50][dg][ilC20]}`
            "
          />
          <div>{{ scope.data.name }}</div>
        </div>
      </template>
      <img class="like-pointer" slot="like" src="~img/like-txt.png" />
      <img class="nope-pointer" slot="nope" src="~img/nope-txt.png" />
      <img class="super-pointer" slot="super" src="~img/super-txt.png" />
      <img class="down-pointer" slot="down" src="~img/down-txt.png" />
      <img class="rewind-pointer" slot="rewind" src="~img/rewind-txt.png" />
    </Tinder>

    <div class="operator">

      <select name="" v-model="selected" @change="selectVal">
        <option
          v-for="option in options"
          :key="option.value"
          :value="option.value"
        >
          {{ option.text }}
        </option>
      </select>
      <a href="https://cbmland.com/symbol-tinder/Focus.html">Focus</a>
      <a href="https://cbmland.com/symbol-tinder/Social.html">Social</a>

    </div>
    
    <div class="btns">
      
      <img src="~img/rewind.png" @click="decide('rewind')" />
      <img src="~img/nope.png" @click="decide('nope')" />
      
      <img src="~img/like.png" @click="decide('like')" />
      <img src="~img/super-like.png" @click="decide('super')" />
      
      <img src="~img/skip.png" @click="decide('skip')" />

    </div>
  </div>
</template>

<script>
import Tinder from '@/components/vue-tinder/Tinder.vue'
import axios from 'axios'

export default {
  name: 'App',
  components: { Tinder },
  data: () => ({
    queue: [],
    offset: 0,
    api_domain: '//sh1.tohaw.com',
    history: [],
    selected: 'AAA',
    source: [],
    options: [
      { text: 'AAA', value: 'AAA' },
      { text: '重点ETF', value: '重点ETF' },
      { text: '半导体', value: '半导体' },
      { text: '金融', value: '金融' },
      { text: '基建', value: '基建' },
      { text: '传统', value: '传统' },
      { text: '出行', value: '出行' },
      { text: '电动车', value: '电动车' },
      { text: '食品饮料', value: '食品饮料' },
      { text: '互联网服务', value: '互联网服务' },
      { text: '服饰', value: '服饰' }
    ]
  }),
  created() {
    this.getSymbols(this.selected)
    //this.mock()
  },
  methods: {
    selectVal: function(ele) {
      this.queue = []
      this.offset = 0
      this.history = []
      this.source = []
      console.log(ele.target.value)
      this.getSymbols(ele.target.value)
    },
    getSymbols(name) {
      let json_url = this.api_domain + '/screener/group-symbols'

      // Make a request for a user with a given ID
      let self = this
      axios
        .post(json_url, { group_name: name })
        //axios.get('/backtrader-master/logs/Toolbox_Screener_Data.json')
        .then(function(response) {
          // handle success
          //console.log(response)

          self.source = response.data.list
          self.mock()
        })
        .catch(function(error) {
          // handle error
          console.log(error)
        })
        .then(function() {
          // always executed
        })
    },
    mock(count = this.source.length, append = true) {
      const list = []
      for (let i = 0; i < count; i++) {
        list.push({
          id: this.source[this.offset].symbol,
          name: this.source[this.offset].name
        })
        this.offset++
      }

      if (append) {
        this.queue = this.queue.concat(list)
      } else {
        this.queue.unshift(...list)
      }
    },
    onSubmit(choice) {
      let item = choice.item
      let json_url = this.api_domain + '/screener/choice'

      if(choice.type != 'skip'){
        
        axios.post(json_url, { symbol: choice.key, type: choice.type })
        //axios.get('/backtrader-master/logs/Toolbox_Screener_Data.json')
        .then(function(response) {
          // handle success
          //console.log(response)
        })
        .catch(function(error) {
          // handle error
          console.log(error)
        })
        .then(function() {
          // always executed
        })

      }



      if (this.queue.length < 3) {
        this.mock()
      }
      this.history.push(item)
    },
    async decide(choice) {
      if (choice === 'rewind') {
        if (this.history.length) {
          //一个个 rewind
          // this.$refs.tinder.rewind([this.history.pop()])
          // 一次性 rewind 全部
          // this.$refs.tinder.rewind(this.history)
          // this.history = []
          // 一次随机 rewind 多个
          this.$refs.tinder.rewind(
            this.history.splice(-1)
          )
          // 非 api调用的添加
          // this.queue.unshift(this.history.pop())
          // this.queue.push(this.history.pop())
          // 非头部添加
          // this.queue.splice(1, 0, this.history.pop())
          // 一次性 rewind 多个，并且含有非头部添加的 item
          // this.queue.unshift(this.history.pop())
          // this.queue.unshift(...this.history)
        }
      } else if (choice === 'help') {
        //
      } else {
        this.$refs.tinder.decide(choice)
      }
    }
  }
}
</script>

